.demo {
  margin: 35px 0;
  &__wrapper {
    overflow: hidden;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-radius: 10px;
    background: #ebf0fa;
  }

  &__content {
    flex: 0 1 930px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 0 72px 0;
  }

  &__title {
    margin-bottom: 18px;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 30px;
    line-height: 160%;
    color: #1f4284;
  }

  &__text {
    max-width: 511px;
    margin-bottom: 36px;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 17px;
    line-height: 160%;
    color: #727272;

  }

  &__btn {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 60px;
    padding:  0 12px 0 24px;
    width: 341px;
    height: 68px;
    background: #244c98;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 160%;
    color: #fff;
  }

  &__btn-img {
    width: 44px;
    height: 44px;
  }

  &__img {
    flex: 0 1 910px;
    min-height: 340px;
    img {
      display: block;
      min-height: 340px;
    }
  }
}

@media(max-width: 1440px) {
  .demo__title {
    font-size: 26px;
  }
}

@media(max-width: 920px) {
  .demo__wrapper {
    display: block;
  }
  .demo__content {
    padding: 24px;
  }
  .demo__title {
    margin-bottom: 18px;
    font-size: 20px;
  }
  .demo__text {
    margin-bottom: 24px;
    font-size: 15px;
    max-width: 100%;
  }
  .demo__btn {
    border-radius: 60px;
    width: 261px;
    height: 56px;
    padding: 0px 10px 0px 18px;
    font-size: 15px;
  }
  .demo__btn-img {
    width: 36px;
    height: 36px;
  }
  .demo__img {
    width: 100%;
    height: auto;
    min-height: inherit;
    img {
      min-height: inherit;
    }
  }

}

@media(max-width: 480px) {
  .demo__content {
    padding-bottom: 18px;
  }
  .demo__title {
    margin-bottom: 14px;
    font-size: 17px;
  }
  .demo__text {
    margin-bottom: 18px;
    font-size: 13px;
  }

}

@media(max-width: 357px) {
  .demo__btn {
    width: 100%;
  }
}

@media(max-width: 330px) {
  .demo__btn {
    padding-left: 7px;
  }
}
