.modal {

  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.3);
  z-index: 1000;

  &__form {

    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border-radius: 0.625rem;
    max-width: 500px;
    width: 100%;
    background: #f0f4fc;
    padding: 24px;
    padding-top: 80px
  }

  &__content {
    margin-bottom: 36px;
  }

  &__close {
    position: absolute;
    right: 24px;
    top: 24px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 10px;
    width: 52px;
    height: 52px;
    background: #fff;
    cursor: pointer;

    img {
      width: 24px;
      height: 24px;
      display: block;
    }
  }

  &__container {
    position: relative;
  }
  &__title {
    padding: 0;
    margin: 0;
    margin-bottom: 18px;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 24px;
    color: #323232;
  }

  &__input-wrap {
    margin-bottom: 24px;
  }

  &__item-input {
    display: block;
    width: 100%;
    height: 73px;
    padding: 0 24px;
    outline: none;
    border: 1px solid rgba(42, 64, 123, 0.5);
    border-radius: 10px;
    background: #d7e2f6;

    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    color: #1f4284;
    &:not(:last-child) {
      margin-bottom: 4px;
    }
  }

  &__item-btn {
    width: 100%;
    margin: 2.25rem 0 2rem;
  }
  &__btn {
    display: block;
    border-radius: 4px;
    padding: 0 12px;
    height: 65px;
    width: 100%;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
  }
  &.hidden {
    display: none;
  }
}


.modal {
  &__label {
    margin-bottom: 28px;
  }
  &__label p{
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 18px;
    color: #323232;

  }

  &__label-checkbox {
    display: flex;
    align-items: center;
    font-weight: 300;
  }

  &__checkbox {
      appearance: none;
  }
  &__checkbox-link {
      text-decoration: underline;
  }
  &__label-checkbox span{
    flex-shrink: 0;
    margin-right: 17px;
    display: inline-block;
    display: flex;
    width: 18px;
    height: 18px;
    border: 2px solid #000;
    border-radius: 4px;
  }
  &__label-checkbox input:checked + span{
    background: url('../img/form-union.svg') center no-repeat;
    background-size: contain;
  }
}

.btn {
  &__restore {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 20px;
    color: #244c98;
  }
  &__sign-in {
    color: #fff;
    background: #244c98;
    margin-bottom: 4px;
  }
  &__register {
    color: #244c98;
    border: 1px solid #244c98;
  }
}




